@import-normalize; /* bring in normalize.css styles */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.avatar {
  height: 100px;
  border-radius: 50%;
}

.profile {
  width: 200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: ghostwhite;
  margin: 0 auto;
  margin-top: 100px;
  padding-top: 30px;
}

.name {
  font-weight: 600;
}

.description {
  text-align: center;
}

.stats {
  list-style: none;
  display: inline-flex;
  background-color: lightsteelblue;
  justify-content: space-between;
  padding: 0;
  margin: 0;
}

.stats > li {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 33%;
  padding: 10px 0;
  margin: 0;
  border-left: 1px solid white;
}

.label {
  font-size: 12px;
}

.stats li:first-child {
  border-left: none;
}

.quantity {
  font-size: 20px;
}

.statistics {
  width: 250px;
  margin: 0 auto;
  text-align: center;
  margin-top: 100px;
  background-color: ghostwhite;
}

.stat-list {
  list-style: none;
  display: flex;
  padding: 0;
}

.title {
  text-transform: uppercase;
  font-weight: 400;
  padding-top: 30px;
  margin: 0;
}

.stat-list > li {
  width: 20%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  height: 50px;
  flex-direction: column;
}

.percentage {
  font-size: 20px;
}